<template lang="pug">
  div.register-container
    header.header-top( @click="$router.go(-1)" )
      span.iconfont.icon-fanhui 
      span 返回
    div.main
      .main-content 
        h1.register-h1 
          span.text  设置密码 
        h2.register-text__sm.pd-85  
          | 填写你的密码
      .main-content
        .register-form-item   
          .register-input 
            input( type="password", placeholder="密码", v-model="password" )
        .register-form-item    
          button.register-btn( @click="next", :class="[isDisable ? 'disable': '']") 注册
</template>
<script>
import { mapActions } from 'vuex'
export default {
  methods: {
    next() {
      if(this.isDisable) return;
      this.register().then(data => {
        this.$router.push({
          name: 'register_success'
        })
      }).catch(err => {
      })
      
    },
    ...mapActions('register', ['register'])
  },
  computed: {
    isDisable() {
      return this.password.length< 3 || this.password.length> 12
    },
    password: {
      get() {
        return this.$store.state.register.password
      },
      set(value) {
        this.$store.commit('register/setField', {value, name: 'password'})
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
  .register-container 
    .header-top 
      padding 1.7rem .5rem 0 .5rem 
      color #737373
    .main  
      padding-top 1.5rem   
    .main-content 
      padding 0 1.6rem
    .register-h1
      font-size 2rem
      color #000
    .register-text__sm 
      font-size .8rem 
      color #737373 
      line-height 1.3rem
    .pd-85 
      padding-top .85rem 
    .register-form-item  
      margin-top 2.1rem   
      .register-input  
        padding-bottom .9rem 
        border-bottom 1px solid #e4e4e4
        input  
          width 100% 
          outline none
          color #bbbbbb 
          font-size 1.1rem
      .register-btn  
        width 100% 
        padding .75rem 0 
        background-color #12b8f6 
        border none 
        border-radius 3px 
        color #ffffff
        font-size 1rem
        &.disable 
          background-color #eaebed 
          color #babbbd
      



      
</style>




